	props:['editor','allchek'],
	components:{
		amount,
		uniPopup
	},
	methods:{
		//弹出模态框
		togglePopup(type, open) {
			switch (type) {
				case 'bottom':
					this.content = '底部弹出 popup'
					break
			}
			this.type = type
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		// 点击选中
		action(item){
			item.action=!item.action
		}
	},
	watch:{
		//监听全选状态，并改变选中
		allchek(newval){
				this.goodList.forEach(item=>{
					item.action=newval
				})
		}
	}
}
